/* Global styles */
body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  padding: 0;
  background: linear-gradient(to bottom, #e8f5e9, #f1f8e9);
  color: #333;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  color: #2e7d32;
}

a {
  color: #4caf50;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #2e7d32;
}

